.main-bar{
    width: calc(100% - 300px - 24px);
    float: left;
    margin: 25px 0px;
    padding: 0;
}
.main-bar-header{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.main-bar-header h1{
    font-weight: 400;

}
.main-bar-header .ask-btn{
    padding: 10px 15px;
    border-radius: 4px;
    text-decoration: none;
    background-color: #009dff;
    color: white;
    border: none;
    transition: 0.3s;
}
.main-bar-header .ask-btn:hover{
    background-color: #0086d8;
}
.display-question-container{
    height: 80px;
    width: 100%;
    display: flex;
    align-items: center;
    background-color: #fdf7e2;
    border-bottom: solid 1px #edeff0;
}
.display-question-container .display-votes-ans{
    padding: 20px;

}
.display-question-container .display-votes-ans p{
    margin: 0%;
    text-align: center;
}
.display-question-details{
    flex-grow: 1;
    padding: 0px 20px;

}
.display-question-details p{
    margin: 0%;
    padding: 4px;
}
.ad-component {
    border: 1px solid #ddd;
    padding: 10px;
    margin: 10px 0;
    background-color: #f9f9f9;
    width: 100%; /* Ensure the ad component takes full width */
    height: 80px;
    box-sizing: border-box;/* Include padding and border in the element's total width and height */
  }
.question-title-link{
    text-decoration: none;
    color: #037ecb;
    transition: 0.3s;
}
.question-title-link:hover{
    color: #009dff;
}
.display-tags-time{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
}
.display-tags-time .display-tags{
    display: flex;
    flex-wrap: wrap;
}
.display-tags-time .display-tags p{
    margin: 2px;
    padding: 4px;
    font-size: 13px;
    background-color: #edeff0;
    color: #39739d;
}
.display-tags-time .display-time{
    
    font-size: 13px;
}


